.no-radius {
  border-radius: 0;
}

.highLightCode {
  display: flex;
  background: #282c34;
  padding: 15px;
  width: 100%;
  overflow: hidden;
  overflow-x: auto;
  border-radius: 0.5rem;
  flex-wrap: wrap;
}
.highLightCode:hover::-webkit-scrollbar {
  height: 5px;
}
.highLightCode::-webkit-scrollbar {
  height: 0px;
}
.highLightCode::-webkit-scrollbar-track {
  background: transparent;
}
.highLightCode::-webkit-scrollbar-thumb {
  background: #a8a8a8;
}
.highLightCode::-webkit-scrollbar-thumb:hover {
  background: #999;
}
.highLightCode .code-comment {
  color: #888;
}
.highLightCode .code-text {
  color: #fff;
}
.highLightCode .code-wrap {
  display: flex;
  width: 100%;
}
.highLightCode .code-bracket {
  color: #88c6be;
}
.highLightCode .code-tag {
  color: #fc929e;
}
.highLightCode .code-prop {
  color: #c5a5c5;
}
.highLightCode .code-value {
  color: #8dc891;
}

.navbar {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  box-shadow: none;
}
.navbar .navbar-menu li .menu-link.is-active {
  color: #111;
  border-radius: 0;
  border-bottom: 1px solid #a8a8a8;
}

.container-max {
  display: flex;
}
.container-max .container-left {
  width: calc(100% - 300px);
  padding: 20px 50px;
}
.container-max .container-right {
  position: sticky;
  top: 55px;
  width: 300px;
  height: calc(100vh - 55px);
  overflow-y: auto;
  background: #fff;
  padding: 0 0 15px 0;
  border-left: 1px solid #d8d8d8;
}
.container-max .container-right::-webkit-scrollbar {
  width: 0px;
}
.container-max .container-right::-webkit-scrollbar-track {
  background: transparent;
}
.container-max .container-right::-webkit-scrollbar-thumb {
  background: #a8a8a8;
}
.container-max .container-right::-webkit-scrollbar-thumb:hover {
  background: #999;
}
.container-max .container-right .menu-right.selected {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  background: #fff;
}
.container-max .container-right .menu-right-title {
  font-weight: bold;
  padding: 10px;
}
.container-max .container-right .menu-right-item {
  margin: 0;
  padding: 0;
  list-style: none;
}
.container-max .container-right .menu-right-item li a.active {
  border-left: 4px solid #000;
}
.container-max .container-right .menu-right-item li a {
  padding: 2px 26px;
  color: #333;
  display: block;
  width: 100%;
  border-left: 4px solid transparent;
  text-decoration: none;
}
.container-max .container-right .menu-right-item li a:hover {
  color: #888;
}
.container-max .container-right-toggle {
  position: fixed;
  cursor: pointer;
  bottom: 0px;
  right: 15px;
  height: 60px;
  width: 60px;
  opacity: 0;
  color: #fff;
  visibility: hidden;
  text-align: center;
  background: #262626;
  border-radius: 50px;
  transition: all 0.15s;
  box-shadow: 0 0 2em 0.2em rgba(0, 0, 0, 0.3);
}

.container-home {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 100%;
  display: flex;
  max-width: 1200px;
  align-items: center;
  flex-direction: row-reverse;
  transform: translate(-50%, -50%);
}
.container-home .ch-left {
  width: 60%;
}
.container-home .ch-right {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container-home .ch-right img {
  width: 350px;
  max-width: 100%;
}

@media screen and (max-width: 992px) {
  .navbar .navbar-menu li .menu-link.is-active {
    color: #111;
    border-radius: 0;
    background: #f2f2f2;
    border-bottom: 1px solid transparent;
  }

  .container-max {
    flex-wrap: wrap;
  }
  .container-max .container-left {
    width: 100%;
    padding: 0 10px;
  }
  .container-max .container-right {
    position: fixed;
    left: 0;
    opacity: 0;
    top: 200px;
    width: 100%;
    visibility: hidden;
    transition: top 0.15s;
  }
  .container-max .container-right::-webkit-scrollbar {
    width: 10px;
  }
  .container-max .container-right.menu-show {
    top: 55px;
    opacity: 1;
    visibility: visible;
  }
  .container-max .container-right-toggle {
    bottom: 15px;
    opacity: 1;
    visibility: visible;
    transition: all 0.15s;
  }

  .container-home {
    width: 95%;
    flex-direction: column-reverse;
  }
  .container-home .ch-left {
    width: 100%;
  }
  .container-home .ch-left .ch-left--intro {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  .container-home .ch-left .ch-left--intro h1 {
    margin-top: 15px;
  }
  .container-home .ch-right {
    width: 100%;
  }
  .container-home .ch-right img {
    width: 350px;
  }
}